import React, { useEffect, useState } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import YourUser from './YourUser';


const useStyles = makeStyles((theme) => ({
    root: {
      width: '95%',
      marginTop:'20px',
      marginLeft:'10px',
      "@media only screen and (min-width: 400px)" : {
        alignSelf:'center',
        width:'96%',
        marginTop:'40px'
      }
    },
    accord : {
      backgroundColor:'transparent'
    },
    demo: {
      backgroundColor: 'inherit',
    },
    title: {
      margin: theme.spacing(4, 0, 2),
      fontWeight:'700'
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: '900',
    },
    lItemText : {
      fontWeight:'900'
    },
    input : {
      width: '58%',
      height:'100%',
      marginRight:'.2em'
    },
    button : {
      width:'15%',
      height:'35px',
      fontWeight:'bold',
      fontSize:'.8em',
    },
  }));
  



export default function YourUsers({highlight, setHighlight}) {
    const [all, setall] = useState([])
    const classes = useStyles();
    const [dense, setDense] = React.useState(true);


    useEffect(() => {
    let allUsers = document.cookie.split(';')
    setall(allUsers)
    }, [highlight])
    
   
    return (
        <div className={classes.root}>
          <Accordion className={classes.accord}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Imtina et / Dəyişiklik et</Typography>
        </AccordionSummary>
        <AccordionDetails>
        <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <div className={classes.demo}>
            <List dense={dense}>
              {all[0] !== '' ? all.map((i,index) => 
              <YourUser key={index} i={i} highlight={highlight} setHighlight={setHighlight} />
              ) : null}
            </List>
          </div>
        </Grid>
      </Grid>
        </AccordionDetails>
      </Accordion>      
     
        </div>
    )
}
